<template>
  <v-container>
    <v-layout row wrap mb-4>
      <v-flex xs12 sm6>
        <h1>Registro diario</h1>
      </v-flex>
      <v-flex xs12 sm6 text-sm-right text-xs-left>
        <v-btn color="success" @click="createWimhof()">Crear nuevo wimHof</v-btn>
      </v-flex>
    </v-layout>
    <router-view></router-view>
    <v-spacer></v-spacer>
    <v-data-table :headers="headers" :items="wimHofs" class="elevation-1">
      <template v-slot:items="props">
        <td class="text-xs-center">{{ props.index+1 }}</td>
        <td class="text-xs-center">
          <router-link :to="{ name: 'wimHofPage',params:{id:props.item._id}}">{{props.item.title}}</router-link>
        </td>
        <td class="text-xs-center">{{ formattedDate(props.item.date) }}</td>
        <td class="text-xs-center">
          <masterChip :colorState="props.item.state" />
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import Axios from "axios";
import { formatDate } from "../mixins/formatDate";
export default {
  mixins: [formatDate],

  data() {
    return {
      headers: [
        { text: "N°", value: "title", sortable: false, align: "center" },
        {
          text: "Título",
          align: "center",
          sortable: false,
          value: "title"
        },
        { text: "Fecha", value: "date", sortable: false, align: "center" },
        { text: "Estado (g)", value: "state", sortable: false, align: "center" }
      ],
      wimHofs: []
    };
  },
  mounted() {
    this.getWimhofs();
  },
  methods: {
    getWimhofs() {
      Axios.get("/api/wimhof/")
        .then(response => {
          console.log(response);
          this.wimHofs = response.data.payload;
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    },
    createWimhof() {
      Axios.post("/api/wimhof", { date: Date.now() })
        .then(response => {
          console.log(response);
          this.wimHofs.push(response.data.payload);
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>