<template>
  <v-container v-if="wimhof">
    <h1>
      <v-layout>
        <v-flex sm6>
          <v-text-field v-model="wimhof.title"></v-text-field>
        </v-flex>
        <v-flex sm6>{{formattedDate(wimhof.date)}}</v-flex>
      </v-layout>
    </h1>
    <masterChip :colorState="wimhof.state" />
    <v-text-field v-model="wimhof.state" label="estado" id="id"></v-text-field>
    <chronometer :laps="wimhof.laps" @stop="getTimeLaps" />
    <p>Notas respecto a la experiencia de hoy:</p>
    <v-textarea
      box
      name="input-7-1"
      label="¿Qué le dirias a tu personal trainer Wim Hof?"
      v-model="wimhof.notes"
      hint="Hint text"
    ></v-textarea>
    <v-btn color="error" :to="{name:'wimHofMethod'}">Volver</v-btn>
    <v-btn color="success" @click="saveIntoDb">Guardar cambios</v-btn>
  </v-container>
</template>

<script>
import Axios from "axios";
import date from "../../tools/customDate";
import { formatDate } from "../mixins/formatDate";
export default {
  mixins: [formatDate],
  data() {
    return {
      wimhof: null
    };
  },
  mounted() {
    this.initData();
    console.log(this.$route.params.id); // prints value of :id
  },
  methods: {
    getTimeLaps(laps) {
      this.wimhof.laps = laps;
      console.log(this.wimhof);
    },
    initData() {
      Axios.get("/api/wimhof/search", {
        params: {
          id: this.routeParams.id
        }
      })
        .then(response => {
          this.wimhof = response.data.payload[0];
          console.log(response.data);
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    },
    saveIntoDb() {
      let id = this.wimhof._id;
      let wimhof = this.wimhof;
      Axios.put("/api/wimhof/" + id, { wimhof })
        .then(response => {
          console.log(response.data.payload);
        })
        .catch(error => {
          // handle error
          console.log(error);
        });
    }
  },
  computed: {
    routeParams() {
      return this.$route.params;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>