import Vue from 'vue'
import Router from 'vue-router'
import {
    dailyGoals,
    dashboard,
    ideas,
    timeManagment,
    campayoMethod,
    wimHofMethod
} from './tools/routePaths'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
        path: '/',
        component: dashboard,
        children: [{
                path: '/',
                name: 'inicio',
                component: require('./views/navigationPages/start').default
            }, {
                path: '/ideas',
                name: 'ideas',
                component: ideas
            },
            {
                path: '/objetivos-diarios',
                name: 'dailyGoals',
                component: dailyGoals
            },
            {
                path: '/administracion-del-tiempo',
                name: 'timeManagment',
                component: timeManagment
            },
            {
                path: '/metodo-ramon-campayo',
                name: 'campayoMethod',
                component: campayoMethod
            },
            {
                path: '/metodo-wim-hof',
                name: 'wimHofMethod',
                component: wimHofMethod,

            }, {
                path: '/metodo-wim-hof/:id',
                name: 'wimHofPage',
                component: require('./views/pages/wimHofPage.vue').default
            }
        ]
    }]
})