<template>
  <v-chip small :color="setColor(colorState)" text-color="white">{{colorState}}</v-chip>
</template>

<script>
export default {
  props: ["colorState"],
  data() {
    return {
      color: null
    };
  },
  methods: {
    setColor(state) {
      switch (state) {
        case "POR HACER":
          return "secondary";
          break;
        case "HACIENDO":
          return "primary";
          break;
        case "HECHO":
          return "green";
          break;
        case "NO HECHO":
          return "red";
          break;
        default:
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
</style>